
import React from 'react';
import './BottomBar.scss';
import {useParams} from 'react-router-dom';

const BottomBar = (props: any) => {
    const location = props.parent.location.pathname;
    const params: any = useParams()
    return (
        <div className="bottomBar">
        <span className={`tab ${location.includes('/list') ? 'hidden': ''}`}>
            <span className={'item'} onClick={() => (props.parent.history.push(`/list/${params.vozac}`))}>Pice</span>
        </span>

        <span className={`tab ${location.includes('/tab1') ? 'hidden': ''}`}>
            <span className={'item'}  onClick={() => (props.parent.history.push(`/tab1/${params.vozac}`))}>Sendviči</span>
        </span>

        <span className={`tab ${location.includes('/tab2') ? 'hidden': ''}`}>
            <span className={'item'} onClick={() => (props.parent.history.push(`/tab2/${params.vozac}`))}>Paste</span>
        </span>

        <span className={`tab ${location.includes('/tab3') ? 'hidden': ''}`}>
            <span className={'item'} onClick={() => (props.parent.history.push(`/tab3/${params.vozac}`))}>Salate</span>
        </span>

        <span className={`tab ${location.includes('/details') ? 'hidden': ''}`}>
            <span className={'item'} onClick={() => (props.parent.history.push(`/details/${params.vozac}`))}>Lista</span>
        </span>
    </div>
    )
}

export default BottomBar