import {
    IonCard,
    IonCardContent,
    IonCardHeader,
    IonCardSubtitle,
    IonCardTitle,
    IonContent,
    IonHeader,
    IonIcon,
    IonItem,
    IonLabel,
    IonList,
    IonListHeader,
    IonPage,
    IonTitle,
    IonToolbar
  } from '@ionic/react';
  import { IonModal, IonButton } from '@ionic/react';
  import firebase from 'firebase';
  import { book, build, colorFill, grid } from 'ionicons/icons';
  import React, {useState, useEffect} from 'react';
  import user_placeholder from './assets/user_placeholder.png';
  import './Auth.scss';
  import {useParams} from 'react-router-dom';
  
  
  const firebaseConfig = {
    apiKey: "AIzaSyA05qpa5yk7L97U4NMIXWucLhDrUPI9seI",
    authDomain: "pizzahot-3c595.firebaseapp.com",
    databaseURL: "https://pizzahot-3c595.firebaseio.com",
    projectId: "pizzahot-3c595",
    storageBucket: "pizzahot-3c595.appspot.com",
    messagingSenderId: "130369379694",
    appId: "1:130369379694:web:71e07af6c7e10d7adf757f",
    measurementId: "G-F4GX93TV9X"
  };
  
  firebase.initializeApp(firebaseConfig);
  
  const Auth: React.FC = (props: any) => {
    const any : any = []
    const database = firebase.database()
    const [users, setUsers] = useState(any)
    const ref = database.ref('menu/pizza')
    const usersRef = database.ref('users')
    const [showModal, setShowModal] = useState(false)
    const [selected, setSelected] = useState(any)
    const [password, setPassword] = useState('')
    const [showError, setShowError] = useState(false)

    const login = (e: any) => {
        e.preventDefault();
        if (selected.password === password) {
            props.history.push(`/list/${selected.name}`);
            let elements = document.querySelectorAll("ion-tab-bar")
            if (elements != null) {
                Object.keys(elements).map((key: any) => {
                    elements[key].style.display = 'inherit'
                });
            }
            setShowModal(false)
        } else {
            setShowError(true)
        }
    }
  
    const getUsers = (data: any) => {
      setUsers(data.val())
      usersRef.off()
    }
  
    const errData = (err: any) => {
      console.log(err.val())
    }
  
    usersRef.once('value', getUsers, errData)

    if (props.location.pathname === '/auth') {
        let elements = document.querySelectorAll("ion-tab-bar")
        if (elements != null) {
            Object.keys(elements).map((key: any) => {
                elements[key].style.display = 'none'
            });
        }
    }

    return (
      <IonPage className={'authPage'}>
        <IonHeader>
          <IonToolbar>
            <IonTitle>Login</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonContent>
          <div className={'user-list'}>
            {
               users.map((item: any) => (
                <div className={'user'} onClick={() => {setSelected(item); setShowModal(true); setShowError(false)}}>
                  <div className='image'>
                    <img src={user_placeholder} />
                  </div>
                  <div className='text'>
                    <p className='name'>{item.name}</p>
                  </div>
                </div>
              ))
            }
          </div>
  
          <IonModal animated isOpen={showModal} onDidDismiss={() => {setShowModal(false); setPassword(''); setShowError(false)}}>
            <div className={'modal-content'}>
                <form onSubmit={(e: any) => {login(e)}}>
                <h3>Password</h3>
                <input type='password' value={password} onChange={(e: any) => {setPassword(e.target.value)}}/>
                <p className={`error ${showError ? 'show' : 'hide'}`} >Pogrešna šifra</p>
              </form>
            </div>

            <div className="buttons">
                <span className={'loginButton back'} onClick={() => {setShowModal(false); setPassword(''); setShowError(false)}}>Nazad</span>
                <span className={'loginButton '} onClick={(e: any) => {login(e)}}>Login</span>
            </div>
          </IonModal>
        </IonContent>
      </IonPage>
    );
  };
  
  export default Auth;  