import {
  IonCard,
  IonCardContent,
  IonCardHeader,
  IonCardSubtitle,
  IonCardTitle,
  IonContent,
  IonHeader,
  IonIcon,
  IonItem,
  IonLabel,
  IonList,
  IonListHeader,
  IonPage,
  IonTitle,
  IonToolbar,
  IonCheckbox
} from '@ionic/react';
import { IonModal, IonButton } from '@ionic/react';
import firebase from 'firebase';
import { book, build, colorFill, grid } from 'ionicons/icons';
import React, {useState, useEffect} from 'react';
import BottomBar from '../components/BottomBar/BottomBar';
import Medal from '../components/modal/modal';
import {useParams} from 'react-router-dom';
import './List/List.scss';
import moment from 'moment'

const Details: React.FC = (props: any) => {
  const database = firebase.database()
  const ordersRef = database.ref('menu')

  const getData = (data: any) => {
    setOrders(data.val())
    console.log(data.val(), 'orders')
    userRef.off();
  }
  
  const errData = (err: any) => {
    console.log(err.val())
  }

  const any : any = []
  const [orders, setOrders] = useState(any)
  const [sides, setSides] = useState(any)
  const [showModal, setShowModal] = useState(false)
  const [showSides, setShowSides] = useState(false)
  const [selected, setSelected] = useState({prices: []})
  const selectedSides = []
  const params: any = useParams()
  const userRef: any = database.ref(`orders/${moment().format('YYYY-MM-DD')}/${params.vozac}`)
  const [sidesCost, setSidesCost] = useState(0)
  const [productCost, setProductCost] = useState(0)
  const selectedDate = moment().format('YYYY-MM-DD')

  setTimeout(() => {
    let elements = document.querySelectorAll("ion-tab-bar")
    Object.keys(elements).map((key: any) => {
        elements[key].style.display = 'inherit'
    });
  }, 300)

  const renderList = () => {
    if (orders === null) {
      return (<IonItem>Nema narudžbina za ovaj datum</IonItem>)
    } else {
      return Object.keys(orders).map((item: any, i: number) => (
          <IonItem className={'artikal'}>Artikal: {orders[item].order.name} - {orders[item].orderPrice} | ukupno: {orders[item].totalCost} | {orders[item].time}</IonItem>
        ))
    }
  }

  userRef.once('value', getData, errData)

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Lista narudzbina</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent>
      <IonList>
        <IonListHeader>
          <IonLabel>Narudzbine za {selectedDate}</IonLabel>
        </IonListHeader>
        {
          renderList()
        }
        </IonList>            
        <BottomBar parent={props}/>
        
      </IonContent>
    </IonPage>
  );
};

export default Details;  