import {
  IonCard,
  IonCardContent,
  IonCardHeader,
  IonCardSubtitle,
  IonCardTitle,
  IonContent,
  IonHeader,
  IonIcon,
  IonItem,
  IonLabel,
  IonList,
  IonListHeader,
  IonPage,
  IonTitle,
  IonToolbar,
  IonCheckbox
} from '@ionic/react';
import { IonModal, IonButton } from '@ionic/react';
import firebase from 'firebase';
import { book, build, colorFill, grid } from 'ionicons/icons';
import React, {useState, useEffect} from 'react';
import pizza_placeholder from './List/assets/salad_placeholder.png';
import BottomBar from '../components/BottomBar/BottomBar';
import Medal from '../components/modal/modal';
import {useParams} from 'react-router-dom';
import './List/List.scss';
import moment from 'moment'

const Tab3: React.FC = (props: any) => {
  const database = firebase.database()
  const menuRef = database.ref('menu')

  const checkId = (itemId: number, sideId: number, reset: boolean, checked: boolean) => {
    if (itemId !== sideId && !reset) {
      return checked;
    } else if (reset) {
      return false;
    }
  }

  const updateSides = (item: any, reset: boolean) => {
    setSides(
      sides.map((side: any) => {
        if (side.id == item.id) {
          setSidesCost(sidesCost + parseInt(side.price))
        }
        return {
          ...side,
          checked: side.id == item.id ? !side.checked : checkId(item.id, side.id, reset, side.checked)
        }
      })
    )
  }

  const getData = (data: any) => {
    setMenu(data.val().SALATE)
    if (sides.length === 0) {
      setSides(data.val().dodaci.map((item: any) => {
        return {
          ...item,
          checked: false
        }
      }))
    }
    menuRef.off();
  }
  
  const errData = (err: any) => {
    console.log(err.val())
  }

  const pickItem = (sides: any) => {
    console.log(selected, sides)
  }

  const finishOrder = () => {
    userRef.child(moment().format('HH:mm:ss')).set({
      time: moment().format('HH:mm:ss YYYY-MM-DD'),
      order: selected,
      orderPrice: productCost,
      sides: sides.filter((side: any) => side.checked),
      sidesCost: sidesCost,
      totalCost: sidesCost + productCost
    });

    updateSides({id: 0}, true)
    setSidesCost(0)
    setProductCost(0)
  }

  const any : any = []
  const [menu, setMenu] = useState(any)
  const [sides, setSides] = useState(any)
  const [showModal, setShowModal] = useState(false)
  const [showSides, setShowSides] = useState(false)
  const [selected, setSelected] = useState({prices: []})
  const selectedSides = []
  const params: any = useParams()
  const userRef: any = database.ref(`orders/${moment().format('YYYY-MM-DD')}/${params.vozac}`)
  const [sidesCost, setSidesCost] = useState(0)
  const [productCost, setProductCost] = useState(0)

  setTimeout(() => {
    let elements = document.querySelectorAll("ion-tab-bar")
    Object.keys(elements).map((key: any) => {
        elements[key].style.display = 'inherit'
    });
  }, 300)

  menuRef.once('value', getData, errData)

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Lista sendviča</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent>
        <div className={'list'}>
          {
             menu.map((item: any) => (
              <div className={'item'} onClick={() => {setSelected(item); setShowModal(true)}}>
                <div className='image'>
                  <img src={pizza_placeholder} />
                </div>
                <div className='text'>
                  <p className='name'>{item.name}</p>
                  <p className='eng'>{item.eng}</p>
                </div>
              </div>
            ))
          }
        </div>

        <IonModal animated isOpen={showModal} onDidDismiss={() => {setShowModal(false);}} >
          <div className={'modal-content'}>
            {
              selected.prices.map((item: any) => (
                <span onClick={() => {setShowModal(false); setShowSides(true); setProductCost(parseInt(item))}}>{item} RSD</span>
              ))
            }
          </div>
          <span className={'button'} onClick={() => {setShowModal(false)}}>NAZAD</span>
        </IonModal>



        <IonModal animated isOpen={showSides} onDidDismiss={() => {setShowSides(false);}} >
          <div className={'modal-content'}>
          <IonList>
            {
              sides.map((side: any) => (
                <IonItem key={side.id} onClick={() => {updateSides(side, false)}}>
                  <IonLabel>{side.name} | {side.price} RSD</IonLabel>
                  <IonCheckbox slot="start" value={side.id} checked={side.checked} />
                </IonItem>
              ))
            }
            </IonList>
          </div>
          <span className={'blue button separated'} onClick={() => {setShowSides(false); finishOrder()}}>SEND</span>
          <span className={'back button'} onClick={() => {setShowSides(false)}}>NAZAD</span>
        </IonModal>
        <BottomBar parent={props}/>
        
      </IonContent>
    </IonPage>
  );
};

export default Tab3;  