import React from 'react';
import { Redirect, Route } from 'react-router-dom';
import {
  IonApp,
  IonIcon,
  IonLabel,
  IonRouterOutlet,
  IonTabBar,
  IonTabButton,
  IonTabs
} from '@ionic/react';
import { IonReactRouter } from '@ionic/react-router';
import {useParams, BrowserRouter} from 'react-router-dom';
import { apps, flash, send } from 'ionicons/icons';
import Tab1 from './pages/Tab1';
import Tab2 from './pages/Tab2';
import Tab3 from './pages/Tab3';
import Details from './pages/Tab4';
import List from './pages/List/List';
import Auth from './pages/Auth/Auth';
import firebase from 'firebase';

/* Core CSS required for Ionic components to work properly */
import '@ionic/react/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/react/css/padding.css';
import '@ionic/react/css/float-elements.css';
import '@ionic/react/css/text-alignment.css';
import '@ionic/react/css/text-transformation.css';
import '@ionic/react/css/flex-utils.css';
import '@ionic/react/css/display.css';

/* Theme variables */
import './theme/variables.css';

const App: React.FC = () => (
  <IonApp>
    <BrowserRouter>
      <Route path="/tab1/:vozac" component={Tab1} exact={true} />
      <Route path="/tab2/:vozac" component={Tab2} exact={true} />
      <Route path="/details/:vozac" component={Details} exact={true}/>
      <Route path="/tab3/:vozac" component={Tab3} exact={true}/>
      <Route path="/list/:vozac" component={List} exact={true}/>
      <Route path="/auth" component={Auth} />
      <Route path="/" render={() => <Redirect to="/auth" />} exact={true} />
    </BrowserRouter>
  </IonApp>
);

export default App;
